<script setup>

import {useRouter} from "vue-router";

const router = useRouter();

const vpnList = [
  {
    id: 3,
    color: 'blue',
    icon: 'mdi-clipboard-text',
    subtitle: 'Действует до: 23.05.2024',
    title: 'Vacation itinerary',
  },
  {
    id: 1,
    color: 'amber',
    icon: 'mdi-gesture-tap-button',
    subtitle: 'Действует до: 22.04.2024',
    title: 'Kitchen remodel',
  },
]

const editProxy = (id) => {
  console.log(id)
  router.push({
    name: 'editProxy',
    query: {
      id: id
    }
  })
}

</script>

