        <template v-slot:append>
          <v-btn
              @click="editProxy(proxyItem.id)"
              color="grey-lighten-1"
              icon="mdi-pencil"
              variant="text"
          ></v-btn>
          <v-btn
              color="grey-lighten-1"
              icon="mdi-qrcode-scan"
